<template>
  <div>
    <v-tabs
      v-model="currentItem"
      color="transparent"
      fixed-tabs
      slider-color="black"
    >
      <v-tab
        v-for="(item,index) in items"
        :key="index"  
        :href="'#tab-' + item.title"
        @click.stop="Filter(item.title)"
      >
        {{ item.title }}
      </v-tab>
      
    </v-tabs>

    <v-tabs-items v-model="currentItem">
      <v-tab-item
        v-for="item in items"
        :key="item.title"
        :value="'tab-' + item.title"
        
      >
        <v-layout row wrap > 
          <v-flex xs3 pt-2 pb-4 px-2 v-for="(image,index) in filteredItems" :key="index">
            <div>
            <v-card elevation="10" transition="slide-x-reverse-transition">
              <v-img :src="image.srcLink" class="grey darken-4"></v-img>              
              <v-card-title v-if="daltonicos" style="background:black;color:white" class="title justify-center">${{image.price}}</v-card-title>
              <v-card-title v-else style="background:red;color:white" class="title justify-center">${{image.price}}</v-card-title>
            </v-card>
            </div>
          </v-flex>
        </v-layout>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import {mapState} from 'vuex'
  export default {
    data: () => ({
      currentItem: 'tab-All',
      imagesLinkMenu:[
          {index:0,srcLink:require("@/assets/images/food_icon01.jpg"),price:"20",category:["Breakfast","All"]},
          {index:1,srcLink:require("@/assets/images/food_icon02.jpg"),price:"20",category:["Special","Dinner","All"]},
          {index:2,srcLink:require("@/assets/images/food_icon03.jpg"),price:"20",category:["Breakfast","Dinner","All"]},
          {index:3,srcLink:require("@/assets/images/food_icon04.jpg"),price:"20",category:["Special","All"]},
          {index:4,srcLink:require("@/assets/images/food_icon05.jpg"),price:"20",category:["Dinner","All"]},
          {index:5,srcLink:require("@/assets/images/food_icon06.jpg"),price:"20",category:["Special","All"]},
          {index:6,srcLink:require("@/assets/images/food_icon07.jpg"),price:"20",category:["Desert","All"]},
          {index:7,srcLink:require("@/assets/images/food_icon08.jpg"),price:"20",category:["Breakfast","Desert","All"]}
        ],
      items: [
        {title:'All'},
        {title:'Breakfast'},
        {title:'Special'},
        {title:'Desert'},        
        {title:'Dinner'}
      ]
      }),
    computed:{
      ...mapState(['daltonicos']),
      filteredItems() {
      var filter = this.currentItem.substr(4,this.currentItem.length-1);
      
      return this.imagesLinkMenu.filter(function(item) {
          return item.category.indexOf(filter) !== -1;
      });
    }
    },
    methods: {
      Filter(title){
        this.currentItem ='tab-'+title;
      }
    }
  }
</script>