<template>
    <div id="app">
        <nav>
            <v-toolbar absolute :dark="daltonicos" class="hidden-sm-and-down">
                <v-toolbar-title>Restaurant</v-toolbar-title>
                <v-spacer></v-spacer>

                <v-toolbar-items v-for="(item,index) in items" :key="index">
                    <v-tooltip bottom>
                        <template v-slot:activator="{ on }">
                            <v-btn v-on="on" v-if="item.title!='CHANGE COLOR'"
                                @click="$vuetify.goTo('#'+item.title,options)" flat>{{item.title}}</v-btn>
                            <v-btn v-on="on" v-else @click="daltonismo" flat>{{item.title}}</v-btn>
                        </template>
                        <span>{{item.title}}</span>
                    </v-tooltip>

                </v-toolbar-items>
                <div>
                    <v-layout row justify-center>
                        <v-dialog v-model="dialog" persistent max-width="600px">
                            <template v-slot:activator="{ on }">
                                <v-btn v-if="login" color="primary" id="logout" dark @click="logout">Logout</v-btn>
                                <v-btn v-else color="primary" id="login" dark v-on="on">Login</v-btn>                                
                                
                            </template>
                           
                            <v-card>

                                <v-card-text>
                                    <v-container grid-list-md>
                                        <v-layout align-center="align-center" justify-center="justify-center">
                                            <v-flex class="login-form text-xs-center">

                                                <v-card light="light">
                                                    <v-card-text>
                                                        <div class="subheading"><template
                                                                v-if="option.isLoggingIn">Login</template><template
                                                                v-else>Crate a new account</template></div>
                                                        <v-form>
                                                            <v-text-field v-model="name"
                                                                prepend-icon="person" id="name" :error-messages="nameErrors"
                                                                :counter="10" label="Name" required
                                                                @input="$v.name.$touch()" @blur="$v.name.$touch()">
                                                            </v-text-field>

                                                            <v-text-field v-model="email" id="email"
                                                                :error-messages="emailErrors" prepend-icon="email"
                                                                label="E-mail" required @input="$v.email.$touch()"
                                                                @blur="$v.email.$touch()"></v-text-field>


                                                            <v-text-field v-model="password" id="password"
                                                             required :error-messages="passwordErrors"  @input="$v.password.$touch()" @blur="$v.password.$touch()"
                                                                prepend-icon="lock" label="Password" type="password">
                                                            </v-text-field>
                                                            <v-btn v-if="option.isLoggingIn" id="sign_in" @click="loginUser"
                                                                block="block">Sign in</v-btn>
                                                            <v-btn v-else block="block" id="sign_up" @click="registro">
                                                                Sign up
                                                            </v-btn>
                                                        </v-form>
                                                    </v-card-text>
                                                </v-card>
                                               
                                                <div v-if="option.isLoggingIn">Don't have an account?
                                                    <v-btn light="light" id="sign_up2" @click="option.isLoggingIn = false">Sign up
                                                    </v-btn>
                                                </div>
                                                <div v-else>
                                                    <v-btn light="light" id="sign_in2" @click="option.isLoggingIn = true">Sign in
                                                    </v-btn>
                                                </div>
                                            </v-flex>
                                        </v-layout>
                                    </v-container>

                                </v-card-text>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="blue darken-1" id="close_login" flat @click="dialog = false">Close</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>
                    </v-layout>
                </div>
            </v-toolbar>
            <div class="hidden-md-and-up">
                <v-expansion-panel :dark="daltonicos">
                    <v-expansion-panel-content>
                        <div slot="header">
                            <v-toolbar-title>Restaurant </v-toolbar-title>
                        </div>
                        <v-card class="text-xs-center" v-for="(item,index) in items" :key="index">
                            <v-btn v-if="item.title!='CHANGE COLOR'" @click="$vuetify.goTo('#'+item.title,options)"
                                flat>{{item.title}}</v-btn>
                            <v-btn v-else @click="daltonismo" flat>{{item.title}}</v-btn>
                        </v-card>
                    </v-expansion-panel-content>
                </v-expansion-panel>
            </div>
        </nav>

    </div>
</template>

<script>
    import * as easings from 'vuetify/es5/util/easing-patterns'
    import {
        validationMixin
    } from 'vuelidate'
    import {
        required,
        maxLength,
        email,
        between,
        minLength,
        numeric
    } from 'vuelidate/lib/validators'
    import {
        mapState,
        mapMutations
    } from 'vuex'
    export default {
        name: 'Menu',
        mixins: [validationMixin],

        validations: {
            name: {
                required,
                minLength: minLength(2),
                maxLength: maxLength(10)
            },
            password: {
                required,
                minLength: minLength(6)
                },
            email: {
                required,
                email
            }
        },
        data() {
            return {
                dialog: false,
                duration: 1500,
                name: '',
                email: '',
                password:'',
                option: {
                    isLoggingIn: true,
                    shouldStayLoggedIn: true,
                },
                offset: 0,
                easing: 'easeInQuad',
                easings: Object.keys(easings),
                items: [{
                        title: "WELCOME"
                    },
                    {
                        title: "ABOUT"
                    },
                    {
                        title: "PRICING"
                    },
                    {
                        title: "BEER"
                    },
                    {
                        title: "BREAD"
                    },
                    {
                        title: "FEATURED"
                    },
                    {
                        title: "RESERVATION"
                    },
                    {
                        title: "CONTACT"
                    },
                    {
                        title: "CHANGE COLOR"
                    }
                ]
            }
        },
        computed: {
            ...mapState(['daltonicos','login']),
            options() {
                return {
                    duration: this.duration,
                    offset: this.offset,
                    easing: this.easing
                }
            },
            nameErrors () {
        const errors = []
        if (!this.$v.name.$dirty) return errors
        !this.$v.name.required && errors.push('Name is required.')        
        !this.$v.name.minLength && errors.push('Name must have at least 2 letters.')
        !this.$v.name.maxLength && errors.push('Name must be at most 10 characters long.')
        return errors
      },     
      passwordErrors () {
        const errors = []
        if (!this.$v.password.$dirty) return errors
        !this.$v.password.required && errors.push('Password is required.')        
        !this.$v.password.minLength && errors.push('Password must have at least 6 letters.')
        return errors
      },     
      emailErrors () {
        const errors = []
        if (!this.$v.email.$dirty) return errors
        !this.$v.email.required && errors.push('E-mail is required.')
        !this.$v.email.email && errors.push('Must be valid e-mail.')
        return errors
      }      
        },

        methods: {
            ...mapMutations(['daltonismo']),
            registro() {

                if (!this.$v.$invalid) {
                    const datos = {
                        name: this.name,
                        email: this.email,
                        password: this.password
                    }
                    this.$store.commit('registro', datos)
                    this.option.isLoggingIn = true
                    this.name=''
                    this.email=''
                    this.password=''
                    
                } else {
                    this.$v.$touch()
                }

            },
            loginUser(){
                console.log(this.$v.$invalid)
                if (!this.$v.$invalid) {
                    const datoUser={
                        name:this.name,
                        email:this.email,
                        password:this.password
                    }
                   this.$store.commit('login', datoUser)
                  if(this.$store.state.login){
                      this.dialog=false
                  }
                } else {
                    this.$v.$touch()
                }
               
            },
            logout(){
                this.$store.commit('logout')
            },
            scroll(target) {
                var ele = document.getElementById(target);
                window.scrollTo(ele.offsetLeft, ele.offsetTop);


            }

        }
    }
</script>
