<template>
  <div>
    <v-container grid-list-md>
      <v-layout row wrap>

        <v-flex xs12 md12>
          <form>
            <v-text-field v-model="name" id="namer" :error-messages="nameErrors" :counter="10" label="Name" required
              @input="$v.name.$touch()" @blur="$v.name.$touch()"></v-text-field>

            <v-text-field v-model="second_name" id="second_name" :error-messages="secondNameErrors" :counter="10" label="Second Name"
              required @input="$v.second_name.$touch()" @blur="$v.second_name.$touch()"></v-text-field>

            <v-text-field v-model="phone" id="phone" :error-messages="phoneErrors" label="Phone" required
              @input="$v.phone.$touch()" @blur="$v.phone.$touch()"></v-text-field>

            <v-text-field v-model="email" id="emailr" :error-messages="emailErrors" label="E-mail" required
              @input="$v.email.$touch()" @blur="$v.email.$touch()"></v-text-field>
            <div class="text-xs-center">
              <v-btn round v-if="daltonicos" id="reservation_button" color="black" :disabled="dialog" :loading="dialog" class="white--text"
                @click="submitForm">
                Send Reservation
              </v-btn>
              <v-btn round v-else id="reservation_button" color="yellow darken-4" :disabled="dialog" :loading="dialog" class="white--text"
                @click="submitForm">
                Send Reservation
              </v-btn>
              <v-dialog v-model="dialog" hide-overlay persistent width="300">
                <v-card color="blue darken-4" dark>
                  <v-card-text>
                    Sending Reservation
                    <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                  </v-card-text>
                </v-card>
              </v-dialog>
              <v-snackbar multi-line='multi-line' id="" v-model="snackbarVisible" right='right' bottom='bottom'
                :timeout='3000' color="success" vertical='vertical'>
                <p class="title">Reservation Sent !</p>
                <v-btn color="white" flat @click="snackbarVisible = false">
                  Close
                </v-btn>
              </v-snackbar>
            </div>
          </form>
        </v-flex>

        <v-flex xs12 md12>
          <v-card>            
            <v-card-title  class="justify-center">
              <div>
                <h2 class="text-center">Hours</h2>                        
              </div>
              
            </v-card-title>
            <v-divider ></v-divider>  
            <v-card-text class="text-xs-center" >
              <p>Monday to Friday: 7:30 AM - 11:30 AM</p>
              <v-divider  ></v-divider>  
                <p>Saturday & Sunday: 8:00 AM - 9:00 AM</p>
                <v-divider  ></v-divider>  
                <p>Monday to Friday: 12:00 PM - 5:00 PM</p>
                <v-divider  ></v-divider>  
                <p>Monday to Saturday: 6:00 PM - 1:00 AM</p>
                <v-divider  ></v-divider>  
                <p>Sunday to Monday: 5:30 PM - 12:00 AM</p>
            </v-card-text>
          </v-card>
        </v-flex>

      </v-layout>
    </v-container>
  </div>
</template>
<script>
  import {mapState} from 'vuex'
  import { validationMixin } from 'vuelidate'
  import { required, maxLength, email, between,minLength, numeric } from 'vuelidate/lib/validators'

  export default {
    mixins: [validationMixin],

    validations: {
      name: { required, minLength: minLength(2),maxLength: maxLength(10) },
      second_name: { required, minLength: minLength(2), maxLength: maxLength(10) },
      phone: { required, minLength:minLength(11), maxLength:maxLength(12)},
      email: { required, email }
    },

    data: () => ({
      name: '',
      second_name:'',
      phone:58,
      email: '',
      dialog: false,
      snackbarVisible:false
    }),
    computed: {

      ...mapState(['daltonicos']),
     
      nameErrors () {
        const errors = []
        if (!this.$v.name.$dirty) return errors
        !this.$v.name.required && errors.push('Name is required.')        
        !this.$v.name.minLength && errors.push('Name must have at least 2 letters.')
        !this.$v.name.maxLength && errors.push('Name must be at most 10 characters long.')
        return errors
      },
      secondNameErrors () {
        const errors = []
        if (!this.$v.second_name.$dirty) return errors        
        !this.$v.second_name.required && errors.push('Second Name is required.')
        !this.$v.name.minLength && errors.push('Second Name must have at least 2 letters.')
        !this.$v.second_name.maxLength && errors.push('Second Name must be at most 10 characters long.')
        return errors
      },
      phoneErrors () {
        const errors = []
        if (!this.$v.phone.$dirty) return errors
        !this.$v.phone.required && errors.push('Phone is required.')
        !this.$v.phone.minLength && errors.push('The phone must have at least 11 numbers.')
        !this.$v.phone.minLength && errors.push('The phone must have a maximum of 12 numbers.')
        return errors
      },
      
      emailErrors () {
        const errors = []
        if (!this.$v.email.$dirty) return errors
        !this.$v.email.required && errors.push('E-mail is required.')
        !this.$v.email.email && errors.push('Must be valid e-mail.')
        return errors
      }      
      
    },

    methods: {      
      submit () {
        console.log(this.$v.$touch())
        this.$v.$touch()
        console.log(this.$v.$invalid)
      },
      submitDialogChange(){
        this.dialog=true
        setTimeout(() => ( this.submitSnackChange()), 4000)
      },
      submitSnackChange(){
        this.dialog=false
        this.snackbarVisible=true
        setTimeout(() => ( this.snackbarVisible=false), 3000)
      },      
      async submitForm(){
        if(!this.$v.$invalid){
          await this.submitDialogChange();
        }else{
          this.$v.$touch()
        }
       
      },


    }
  }
</script>