import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    daltonicos:false,
    
    color:"background:white;color:black;",
    user:[],
    login:false
  },
  mutations: {
    daltonismo(){
      this.state.daltonicos=!this.state.daltonicos
      if(this.state.daltonicos){
        this.state.color="background:black;color:white;"
      }else{
        this.state.color="background:white;color:black;"
      }
      
    },
    registro(state,datos){
      state.user.push(datos)
      console.log(state.user)
    },
    login(state,datos){
      console.log(state.user)
      for(var i = 0; i < state.user.length; i++) {
        if (state.user[i].password== datos.password && state.user[i].email== datos.email) {
          
              state.login=true
            break;
        }else{
        }
    }
    },
    logout(state){
      state.login=false
    }
  },
  actions: {

  }
})
